import React, { useEffect, useState } from "react";
import classnames from "classnames";

const FormCheck = ({ color, selected, onChange }) => {
  const checked = selected && selected === color.id ? true : false;
  return (
    <div className="form-check">
      <input
        className="form-check-input theme-color"
        type="radio"
        name="bgcolor-radio"
        id={color.id}
        onChange={() => {
          onChange(color);
        }}
        checked={checked}
      />
      <label className="form-check-label avatar-xs" htmlFor={color.id}>
        <span
          className={classnames(
            "avatar-title",
            "rounded-circle",
            "theme-btn",
            [color.color],
            [color.id]
          )}
          id={`color-${color.id}`}
        ></span>
      </label>
    </div>
  );
};

const ThemeColor = ({ theme, onChangeData, user }) => {
  const [themeColors] = useState([
    { id: "bgcolor-radio1", color: "bg-blue" },
    { id: "bgcolor-radio2", color: "bg-info" },
    { id: "bgcolor-radio3", color: "bg-purple" },
    { id: "bgcolor-radio4", color: "bg-pink" },
    { id: "bgcolor-radio5", color: "bg-danger" },
    { id: "bgcolor-radio6", color: "bg-warning" },
    { id: "bgcolor-radio7", color: "bg-success" },
  ]);

  const [selected, setSelected] = useState();

  const onChangeThemeColor = (color) => {
    setSelected(color);
    onChangeData("theme", { ...theme, color: color.id });
  };

  useEffect(() => {
    if (user?.themeColor) {
      setSelected(user?.themeColor);
    }
    // eslint-disable-next-line
  }, [user?.themeColor]);
  return (
    <div>
      <h5 className="mb-3 font-size-11 text-muted text-uppercase">
        Choose Theme Color :
      </h5>
      <div className="d-flex align-items-center flex-wrap gap-2 theme-btn-list theme-color-list">
        {(themeColors || []).map((color, key) => (
          <FormCheck
            color={color}
            key={key}
            selected={selected}
            onChange={onChangeThemeColor}
          />
        ))}
      </div>
    </div>
  );
};

export default ThemeColor;
