import chatActions from "./action";

const initState = {
  contacts: [],
  selectedChat: "",
};

export default function rootReducer(state = initState, action) {
  switch (action.type) {
    case chatActions.CONTACTS:
      return {
        ...state,
        contacts: action.contacts,
      };

    case chatActions.SELECTEDCHAT:
      return {
        ...state,
        selectedChat: action.selectedChat,
      };

    default:
      return state;
  }
}
