import React, { useEffect, useState } from "react";

import { Link, withRouter } from "react-router-dom";
import {
  Nav,
  NavItem,
  Dropdown,
  DropdownItem,
  DropdownToggle,
  DropdownMenu,
  NavLink,
  UncontrolledTooltip,
} from "reactstrap";

// hooks
import { useRedux } from "../../hooks/index";

// actions
import { changeTab } from "../../redux/actions";

// costants
import { TABS } from "../../constants/index";
import LightDarkMode from "../../components/LightDarkMode";

//images
// import avatar1 from "../../assets/images/users/avatar-1.jpg";

// menu
import { MENU_ITEMS } from "./menu";
import AuthActions from "../../redux/auth/actions";
import NavigationAction from "../../redux/navigation/actions";
import { compose } from "redux";
import { connect } from "react-redux";
import { logout } from "../../api/auth";
import classNames from "classnames";
import config from "../../config";

let { logoutUser } = AuthActions;
let { fetching, success, error } = NavigationAction;

const LogoLightSVG = () => {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="30"
      height="30"
      viewBox="0 0 24 24"
    >
      <path d="M8.5,18l3.5,4l3.5-4H19c1.103,0,2-0.897,2-2V4c0-1.103-0.897-2-2-2H5C3.897,2,3,2.897,3,4v12c0,1.103,0.897,2,2,2H8.5z M7,7h10v2H7V7z M7,11h7v2H7V11z" />
    </svg>
  );
};

const LogoDarkSVG = () => {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="30"
      height="30"
      viewBox="0 0 24 24"
    >
      <path d="M8.5,18l3.5,4l3.5-4H19c1.103,0,2-0.897,2-2V4c0-1.103-0.897-2-2-2H5C3.897,2,3,2.897,3,4v12c0,1.103,0.897,2,2,2H8.5z M7,7h10v2H7V7z M7,11h7v2H7V11z" />
    </svg>
  );
};

const Logo = () => {
  return (
    <div className="navbar-brand-box">
      <Link to="#" className="logo logo-dark">
        <span className="logo-sm">
          <LogoLightSVG />
        </span>
      </Link>

      <Link to="#" className="logo logo-light">
        <span className="logo-sm">
          <LogoDarkSVG />
        </span>
      </Link>
    </div>
  );
};

const themeColors = [
  { id: "bgcolor-radio1", color: "bg-blue" },
  { id: "bgcolor-radio2", color: "bg-info" },
  { id: "bgcolor-radio3", color: "bg-purple" },
  { id: "bgcolor-radio4", color: "bg-pink" },
  { id: "bgcolor-radio5", color: "bg-danger" },
  { id: "bgcolor-radio6", color: "bg-warning" },
  { id: "bgcolor-radio7", color: "bg-success" },
];

const MenuNavItem = ({ item, selectedTab, onChangeTab, user }) => {
  let themeColor = "";
  let themeColorClass = themeColors
    // eslint-disable-next-line array-callback-return
    .map((color) => {
      if (color.id === user?.themeColor) {
        themeColor = color.color;
      }
    });

  if (selectedTab === item.tabId) {
    themeColorClass = themeColor?.replace("bg-", "text-");
  }

  const onClick = () => {
    onChangeTab(item.tabId);
  };

  return (
    <>
      <NavItem className={item.className} id={`${item.key}-container`}>
        <NavLink
          href="#"
          active={selectedTab === item.tabId}
          id={item.key}
          role="tab"
          onClick={onClick}
        >
          <i className={classNames([item.icon], [themeColorClass])}></i>
        </NavLink>
      </NavItem>
      <UncontrolledTooltip target={`${item.key}-container`} placement="right">
        {item.tooltipTitle}
      </UncontrolledTooltip>
    </>
  );
};

const ProfileDropdownMenu = ({
  onChangeTab,
  token,
  history,
  logoutUser,
  fetching,
  success,
  error,
  user,
}) => {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const toggle = () => setDropdownOpen(!dropdownOpen);

  const handleLogout = () => {
    fetching(true);
    logout(token)
      .then((res) => {
        if (res.success) {
          success(res.message);
          fetching(false);
          history.push("/logout");
          logoutUser();
        } else {
          fetching(false);
          error(res.message);
        }
      })
      .catch((err) => {
        console.log(err);
        fetching(false);
      });
  };
  return (
    <Dropdown
      nav
      isOpen={dropdownOpen}
      className="profile-user-dropdown"
      toggle={toggle}
    >
      <DropdownToggle nav className="bg-transparent">
        {user?.profile && (
          <img
            src={`${config.BACKENDURL}/profile/${user?.profile}`}
            alt=""
            className="profile-user rounded-circle"
          />
        )}
      </DropdownToggle>
      <DropdownMenu>
        <DropdownItem
          className="d-flex align-items-center justify-content-between"
          onClick={() => onChangeTab(TABS.USERS)}
        >
          Profile <i className="bx bx-user-circle text-muted ms-1"></i>
        </DropdownItem>
        <DropdownItem
          className="d-flex align-items-center justify-content-between"
          onClick={() => onChangeTab(TABS.SETTINGS)}
        >
          Setting <i className="bx bx-cog text-muted ms-1"></i>
        </DropdownItem>
        {/* <DropdownItem
          className="d-flex align-items-center justify-content-between"
          href="/change-password"
        >
          Change Password <i className="bx bx-lock-open text-muted ms-1"></i>
        </DropdownItem> */}

        <DropdownItem />
        <DropdownItem
          className="d-flex align-items-center justify-content-between cursor-poiner"
          tag="a"
          onClick={handleLogout}
        >
          Log out <i className="bx bx-log-out-circle text-muted ms-1"></i>
        </DropdownItem>
      </DropdownMenu>
    </Dropdown>
  );
};

const SideMenu = ({
  onChangeLayoutMode,
  logoutUser,
  token,
  history,
  fetching,
  success,
  error,
  user,
}) => {
  // global store
  const { dispatch, useAppSelector } = useRedux();

  const menuItems = MENU_ITEMS;
  const { activeTab, layoutMode } = useAppSelector((state) => ({
    activeTab: state.Layout.activeTab,
    layoutMode: state.Layout.layoutMode,
  }));

  /* 
    tab activation
    */
  const [selectedTab, setSelectedTab] = useState(TABS.CHAT);
  const onChangeTab = (id) => {
    setSelectedTab(id);
    dispatch(changeTab(id));
  };

  useEffect(() => {
    setSelectedTab(activeTab);
  }, [activeTab]);

  return (
    <div className="side-menu flex-lg-column">
      {/* LOGO */}
      <Logo />
      {/* end navbar-brand-box */}

      {/* Start side-menu nav */}
      <div className="flex-lg-column my-0 sidemenu-navigation">
        <Nav pills className="side-menu-nav " role="tablist">
          {(menuItems || []).map((item, key) => (
            <MenuNavItem
              item={item}
              key={key}
              selectedTab={selectedTab}
              onChangeTab={onChangeTab}
              user={user}
            />
          ))}

          {/* change mode */}
          <LightDarkMode
            layoutMode={layoutMode}
            onChangeLayoutMode={onChangeLayoutMode}
          />

          {/* profile menu dropdown */}
          <ProfileDropdownMenu
            token={token}
            history={history}
            logoutUser={logoutUser}
            fetching={fetching}
            success={success}
            error={error}
            onChangeTab={onChangeTab}
            user={user}
          />
        </Nav>
      </div>
      {/* end side-menu nav */}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
    user: state.Navigation?.user,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, { logoutUser, fetching, success, error })
)(SideMenu);
