import React from "react";

const UserDescription = ({ userDetails }) => {
  return (
    <>
      <div className="text-muted">
        <p className="mb-4">
          {userDetails && userDetails.description
            ? userDetails.description
            : "-"}
        </p>
      </div>

      <div>
        <div className="d-flex py-2">
          <div className="flex-shrink-0 me-3">
            <i className="bx bx-user align-middle text-muted"></i>
          </div>
          <div className="flex-grow-1">
            <p className="mb-0">
              {userDetails && userDetails?.first_name && userDetails?.last_name
                ? userDetails?.first_name + " " + userDetails?.last_name
                : "-"}
            </p>
          </div>
        </div>

        <div className="d-flex py-2">
          <div className="flex-shrink-0 me-3">
            <i className="bx bx-message-rounded-dots align-middle text-muted"></i>
          </div>
          <div className="flex-grow-1">
            <p className="mb-0">
              {userDetails && userDetails.email ? userDetails.email : "-"}
            </p>
          </div>
        </div>

        <div className="d-flex py-2">
          <div className="flex-shrink-0 me-3">
            <i className="bx bx-location-plus align-middle text-muted"></i>
          </div>
          <div className="flex-grow-1">
            <p className="mb-0">
              {userDetails && userDetails?.location
                ? userDetails.location
                : "-"}
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default UserDescription;
