import React, { useEffect } from "react";
import classnames from "classnames";

// hooks
import { useRedux } from "../../hooks/index";

// hooks
import { useConversationUserType } from "../../hooks/index";

// component
import Leftbar from "./Leftbar";
import ConversationUser from "./ConversationUser/index";
import UserProfileDetails from "./UserProfileDetails/index";
import Welcome from "./ConversationUser/Welcome";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";

const Index = (props) => {
  const { token, isUserLogin, history, user } = props;
  // global store
  const { useAppSelector } = useRedux();

  const { selectedChat } = useAppSelector((state) => ({
    selectedChat: state.Chats.selectedChat,
  }));

  const { isChannel } = useConversationUserType();

  useEffect(() => {
    if (token && isUserLogin) {
      setTimeout(() => {
        history.push("/dashboard");
      }, 500);
    } else {
      history.push("/login");
    }
    // eslint-disable-next-line
  }, [token, isUserLogin]);

  return (
    <>
      <Leftbar />

      <div
        className={classnames(
          "user-chat",
          "w-100",
          "overflow-hidden",
          {
            "user-chat-show": selectedChat,
          },
          [user?.themeBackground]
        )}
        id="user-chat"
      >
        <div className="user-chat-overlay" id="user-chat-overlay"></div>
        {selectedChat !== null ? (
          <div className="chat-content d-lg-flex">
            <div className="w-100 overflow-hidden position-relative">
              <ConversationUser isChannel={isChannel} />
            </div>
            <UserProfileDetails isChannel={isChannel} />
          </div>
        ) : (
          <Welcome />
        )}
      </div>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
    user: state.Navigation.user,
  };
};

export default compose(withRouter, connect(mapStateToProps, null))(Index);
