import { toast } from "react-toastify";
import { CheckUser } from "../../api/auth";

const navigationAction = {
  SUCCESS: "SUCCESS",
  ERROR: "ERROR",
  CLOSE: "CLOSE",
  FETCHING: "FETCHING",
  SET_USER: "SET_USER",

  success: (messages) => {
    messages !== "" &&
      toast.success(messages, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "dark",
      });
    return {
      type: navigationAction.SUCCESS,
      resType: "success",
      message: messages,
      show: true,
      isFetching: false,
    };
  },
  error: (messages) => {
    messages !== "" &&
      toast.error(messages, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "dark",
      });
    return {
      type: navigationAction.ERROR,
      resType: "error",
      message: messages,
      show: true,
      isFetching: false,
    };
  },
  fetching: (flag) => ({
    type: navigationAction.FETCHING,
    isFetching: flag,
  }),

  check: (token) => async (dispatch) => {
    try {
      var data = {};
      await CheckUser(token).then(async (res) => {
        if (res.success) {
          data = res.data;
          return dispatch({
            type: navigationAction.SET_USER,
            user: res.data,
          });
        } else {
          return dispatch({
            type: navigationAction.SET_USER,
            user: res.data,
          });
        }
      });
    } catch (err) {
      return dispatch({
        type: navigationAction.SET_USER,
        user: data,
      });
    }
  },
};

export default navigationAction;
