/*
pages
*/
//root
import Root from "../pages/Root/index";
import Login from "../pages/Authentication/Login";
import Logout from "../pages/Authentication/Logout";
import Register from "../pages/Authentication/Register";
import RecoverPassword from "../pages/Authentication/RecoverPassword";
// import ChangePassword from "../pages/Authentication/ChangePassword";
import LockScreen from "../pages/Authentication/LockScreen";

// dashboard
import Dashboard from "../pages/Dashboard/index";
import StarterPage from "../pages/StarterPage/index";

const publicRoutes = [
  { path: "/login", component: Login },
  { path: "/register", component: Register },
  // { path: "/change-password", component: ChangePassword },
  { path: "/reset-password", component: RecoverPassword },
  { path: "/lock-screen", component: LockScreen },
  { path: "/logout", component: Logout },
];

const privateRoutes = [
  { path: "/pages-starter", component: StarterPage },
  { path: "/dashboard", component: Dashboard },
  { path: "/", exact: true, component: Root },
];

export { publicRoutes, privateRoutes };
