import React, { useEffect, useState } from "react";
import {
  Label,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import classnames from "classnames";

// CONSTANTS
import { STATUS_TYPES } from "../../../constants";
import { uploadProfile, updateStatus } from "../../../api/user";
import config from "../../../config";

const UserProfile = ({ props }) => {
  const { setFieldValue, check, fetching, success, error, token, user } = props;
  const [profile, setProfile] = useState("");
  const fullName = user ? `${user?.first_name} ${user?.last_name}` : "-";

  const onChangeProfile = () => {
    if (profile.name) {
      let data = new FormData();
      data.append("profile", profile);
      uploadProfile(token, data)
        .then((res) => {
          if (res.success) {
            check(token);
            fetching(false);
            success(res.message);
            setProfile("");
          } else {
            error(res.message);
            fetching(false);
            setProfile("");
          }
        })
        .catch((err) => {
          console.log(err);
          fetching(false);
          setProfile("");
        });
    }
  };

  /* drop down state */
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const toggle = () => setDropdownOpen(!dropdownOpen);

  /* status */
  const [userStatus, setUserStatus] = useState(user?.status);
  const onChangeStatus = (status) => {
    setUserStatus(status);
    fetching();
    updateStatus(token, { status })
      .then((res) => {
        if (res.success) {
          fetching(false);

          success(res.message);
          check(token);
        } else {
          fetching(false);

          error(res.message);
          check(token);
        }
      })
      .catch((err) => {
        console.log(err);
        fetching(false);
      });
  };
  useEffect(() => {
    if (profile.name) {
      onChangeProfile();
    }
    // eslint-disable-next-line
  }, [userStatus, profile.name]);

  return (
    <div className="text-center p-3 p-lg-4 border-bottom pt-2 pt-lg-2 mt-n5 position-relative">
      <div className="mb-3 profile-user">
        {user?.profile && (
          <img
            src={`${config.BACKENDURL}/profile/${user?.profile}`}
            className="rounded-circle avatar-lg img-thumbnail user-profile-image"
            alt="user-profile"
          />
        )}

        <div className="avatar-xs p-0 rounded-circle profile-photo-edit">
          <input
            id="profile-img-file-input"
            type="file"
            className="profile-img-file-input"
            accept="image/png, image/jpeg"
            onChange={(e) => {
              if (e.target.files.length > 0) {
                var fileArr = Array.from(e.target.files);
                if (fileArr?.length > 0) {
                  setProfile(fileArr[0]);
                  setFieldValue("profile", fileArr);
                } else {
                  setFieldValue("profile", []);
                  setProfile("");
                }
              }
            }}
          />
          <Label
            htmlFor="profile-img-file-input"
            className="profile-photo-edit avatar-xs"
          >
            <span className="avatar-title rounded-circle bg-light text-body">
              <i className="bx bxs-camera"></i>
            </span>
          </Label>
        </div>
      </div>

      <h5 className="font-size-16 mb-1 text-truncate">{fullName}</h5>

      <Dropdown
        className="d-inline-block"
        isOpen={dropdownOpen}
        toggle={toggle}
      >
        <DropdownToggle tag="a" className="text-muted d-block" role="button">
          <i
            className={classnames(
              "bx",
              "bxs-circle",
              "font-size-10",
              "align-middle",
              { "text-success": user?.userStatus === STATUS_TYPES.ACTIVE },
              { "text-warning": user?.userStatus === STATUS_TYPES.AWAY },
              {
                "text-danger": user?.userStatus === STATUS_TYPES.DO_NOT_DISTURB,
              }
            )}
          ></i>{" "}
          {user?.userStatus?.charAt(0).toUpperCase() +
            user?.userStatus?.slice(1)}{" "}
          <i className="mdi mdi-chevron-down"></i>
        </DropdownToggle>

        <DropdownMenu>
          <DropdownItem onClick={() => onChangeStatus(STATUS_TYPES.ACTIVE)}>
            <i className="bx bxs-circle text-success font-size-10 me-1 align-middle"></i>{" "}
            Active
          </DropdownItem>
          <DropdownItem onClick={() => onChangeStatus(STATUS_TYPES.AWAY)}>
            <i className="bx bxs-circle text-warning font-size-10 me-1 align-middle"></i>{" "}
            Away
          </DropdownItem>
          <DropdownItem
            onClick={() => onChangeStatus(STATUS_TYPES.DO_NOT_DISTURB)}
          >
            <i className="bx bxs-circle text-danger font-size-10 me-1 align-middle"></i>{" "}
            Do not disturb
          </DropdownItem>
        </DropdownMenu>
      </Dropdown>
    </div>
  );
};

export default UserProfile;
