import { api, handleResponse, handleError } from "../services/ApiServices";

export const getPrivacySettings = (token, data) =>
  api(token)
    .get("/user/settings/read/privacy", data)
    .then(handleResponse)
    .catch(handleError);

export const updateThemeSettings = (token, data) =>
  api(token)
    .put("/user/settings/update/theme", data)
    .then(handleResponse)
    .catch(handleError);

export const updatePrivacySettings = (token, data) =>
  api(token)
    .put("/user/settings/update/privacy", data)
    .then(handleResponse)
    .catch(handleError);

export const updateChangePassword = (token, data) =>
  api(token)
    .put("/user/settings/update/password", data)
    .then(handleResponse)
    .catch(handleError);
