import React, { useEffect, useState } from "react";
import {
  // Alert,
  Row,
  Col,
  Form,
  Label,
  Button,
  // UncontrolledTooltip,
} from "reactstrap";

//Social Media Imports
// import { GoogleLogin } from "react-google-login";
// import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";

// router
import { withRouter } from "react-router-dom";

// import { useForm } from "react-hook-form";

// config
// import config from "../../config";

// hooks
// import { useProfile } from "../../hooks/index";
import NavigationActions from "../../redux/navigation/actions";

// components
import NonAuthLayoutWrapper from "../../components/NonAutnLayoutWrapper";
import AuthHeader from "../../components/AuthHeader";
import FormInput from "../../components/FormInput";
import Loader from "../../components/Loader";
import { compose } from "redux";
import formikEnhancer from "./Enhancers/LoginEnhancer";
import { connect } from "react-redux";
import ErrorMsg from "../../components/ErrorMsg";
import { login } from "../../api/auth";
import AuthActions from "../../redux/auth/actions";

const { fetching, success, error } = NavigationActions;
const { loginData } = AuthActions;

const Login = (props) => {
  // global store
  const {
    loginData,
    history,
    errors,
    isFetching,
    touched,
    submitCount,
    handleChange,
    handleBlur,
    values,
    fetching,
    success,
    error,
    token,
    isUserLogin,
  } = props;
  // eslint-disable-next-line
  const [rememberMe, setRememberMe] = useState(true);

  const Error = (props) => {
    const field = props.field;
    if ((errors[field] && touched[field]) || submitCount > 0) {
      return <ErrorMsg message={errors[field]} />;
    } else {
      return <span />;
    }
  };

  const handleLogin = (e) => {
    e.preventDefault();
    let { values, isValid, handleSubmit } = props;
    handleSubmit();
    if (isValid) {
      fetching(true);
      login(values)
        .then(async (res) => {
          if (res.success) {
            loginData(res.data);
            success(res.message);
            if (rememberMe) {
              var date = new Date();
              date.setDate(date.getDate() + 7);
              document.cookie = `token=${res.data.token} ;SameSite=strict;expires=${date}`;
            }
            setTimeout(() => {
              history.push("/dashboard");
            }, 500);
          } else {
            fetching(false);
            error(res.message);
          }
        })
        .catch((err) => {
          console.log(err);
          fetching(false);
        });
    }
  };

  useEffect(() => {
    if (token && isUserLogin) {
      setTimeout(() => {
        fetching(false);
        history.push("/dashboard");
      }, 500);
    } else {
      history.push("/login");
    }
    // eslint-disable-next-line
  }, [token, isUserLogin]);

  return (
    <NonAuthLayoutWrapper>
      <Row className=" justify-content-center my-auto">
        <Col sm={8} lg={6} xl={5} className="col-xxl-4">
          <div className="py-md-5 py-4">
            <AuthHeader
              title="Welcome Back !"
              subtitle="Sign in to continue to Chat Portal."
            />

            {/* {error && <Alert color="danger">{error}</Alert>} */}

            <Form onSubmit={handleLogin} className="position-relative">
              {isFetching && <Loader />}
              <div className="mb-3">
                <FormInput
                  label="Username"
                  type="text"
                  name="email"
                  labelClassName="form-label"
                  placeholder="Enter username"
                  className="form-control"
                  values={values.email}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <Error field="email" />
              </div>

              <div className="mb-3">
                <FormInput
                  label="Password"
                  type="password"
                  name="password"
                  labelClassName="form-label"
                  className="form-control"
                  placeholder="Enter Password"
                  values={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <Error field="password" />
              </div>

              <div className="form-check form-check-info font-size-16">
                <input
                  className="form-check-input"
                  type="checkbox"
                  id="remember-check"
                />
                <Label
                  className="form-check-label font-size-14"
                  htmlFor="remember-check"
                >
                  Remember me
                </Label>
              </div>

              <div className="text-center mt-4">
                <Button className="w-100 auth-bg-btn" type="submit">
                  Log In
                </Button>
              </div>
            </Form>
          </div>
        </Col>
      </Row>
    </NonAuthLayoutWrapper>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
  };
};

export default compose(
  withRouter,
  formikEnhancer,
  connect(mapStateToProps, { loginData, fetching, success, error })
)(Login);
