import authAction from "./actions";
import Cookie from "js-cookie";
const initState = {
  isUserLogin: Cookie.get("isUserLogin")
    ? Cookie.get("isUserLogin") === "true"
    : false,
  user_token: Cookie.get("user_token")
    ? Cookie.get("user_token")
    : document.cookie.split("=").pop(),
};

export default function rootReducer(state = initState, action) {
  switch (action.type) {
    case authAction.LOGIN:
      return {
        ...state,
        isUserLogin: action.isUserLogin,
        user_token: action.user_token,
      };

    case authAction.LOGOUT:
      return {
        ...state,
        isUserLogin: action.isUserLiogn,
        user_token: action.user_token,
        user: action.user,
      };
    default:
      return state;
  }
}
