import { api, handleResponse, handleError } from "../services/ApiServices";

export const searchUser = (token, keyword, message) =>
  api(token)
    .get(`/users/search/${keyword}`)
    .then(handleResponse)
    .catch(handleError);

export const uploadProfile = (token, data) =>
  api(token)
    .put("/users/upload/profile", data)
    .then(handleResponse)
    .catch(handleError);

export const uploadCoverImage = (token, data) =>
  api(token)
    .put("/users/upload/cover-image", data)
    .then(handleResponse)
    .catch(handleError);

export const updateStatus = (token, data) =>
  api(token)
    .put("/users/update/status", data)
    .then(handleResponse)
    .catch(handleError);

export const updateLastConversationId = (token, data) =>
  api(token)
    .put("/users/last-conversation-id/update", data)
    .then(handleResponse)
    .catch(handleError);
