const settingsAction = {
  SETTINGS: "SETTINGS",

  privacy: (data) => ({
    type: settingsAction.SETTINGS,
    displayprofilePhoto: data?.profilePhotoSeen,
    displayLastSeen: data?.lastSeen,
    displayStatus: data?.statusSeen,
    readReceipts: data?.readReceipts,
    displayGroups: data?.groupsSeen,
    ssn: data?.ssn,
  }),
};

export default settingsAction;
