import React from "react";

// components
import ThemeColor from "./ThemeColor";
import ThemeImage from "./ThemeImage";

const ThemeSettings = ({ user, theme, onChangeData }) => {
  return (
    <div className="accordion-body">
      <ThemeColor theme={theme} user={user} onChangeData={onChangeData} />

      <ThemeImage theme={theme} user={user} onChangeData={onChangeData} />
    </div>
  );
};

export default ThemeSettings;
