import React, { useState } from "react";
import { Link } from "react-router-dom";
import classnames from "classnames";

import { STATUS_TYPES } from "../../../constants";
import config from "../../../config";
import { updateLastConversationId } from "../../../api/user";

const ChatUser = ({ token, contact, onSelectChat, selectedChat }) => {
  const fullName = `${contact?.friend_id?.first_name} ${contact?.friend_id?.last_name}`;
  const shortName = `${contact?.friend_id?.first_name?.charAt(
    0
  )} ${contact?.friend_id?.last_name.charAt(0)}`;

  const colors = [
    "bg-primary",
    "bg-danger",
    "bg-info",
    "bg-warning",
    "bg-secondary",
    "bg-pink",
    "bg-purple",
  ];
  const [color] = useState(Math.floor(Math.random() * colors.length));
  const isOnline =
    contact?.friend_id?.status &&
    contact?.friend_id?.status === STATUS_TYPES.ACTIVE;
  const unRead = contact?.meta && contact?.meta?.unRead;

  let selectChat = selectedChat?._id ? selectedChat?._id : selectedChat;
  const isSelectedChat = contact?.friend_id?._id === selectChat ? true : false;
  const onClick = () => {
    onSelectChat(contact?.friend_id);
    updateLastConversationId(token, { user_id: contact?.friend_id?._id });
  };

  return (
    <li className={classnames({ active: isSelectedChat })} onClick={onClick}>
      <Link to="#" className={classnames({ "unread-msg-user": unRead })}>
        <div className="d-flex align-items-center">
          <div
            className={classnames(
              "chat-user-img",
              "align-self-center",
              "me-2",
              "ms-0",
              { online: isOnline }
            )}
          >
            {contact?.friend_id?.profile ? (
              <>
                <img
                  src={`${config.BACKENDURL}/profile/${contact?.friend_id?.profile}`}
                  className="rounded-circle avatar-xs"
                  alt=""
                />
                {isOnline && <span className="user-status"></span>}
              </>
            ) : (
              <div className="avatar-xs">
                <span
                  className={classnames(
                    "avatar-title",
                    "rounded-circle",
                    "text-uppercase",
                    "text-white",
                    colors[color]
                  )}
                >
                  <span className="username"> {shortName}</span>
                  <span className="user-status"></span>
                </span>
              </div>
            )}
          </div>
          <div className="overflow-hidden">
            <p className="text-truncate mb-0">{fullName}</p>
          </div>
          {unRead && unRead !== 0 ? (
            <div className="ms-auto">
              <span className="badge badge-soft-dark rounded p-1">
                {unRead}
              </span>
            </div>
          ) : null}
        </div>
      </Link>
    </li>
  );
};

export default ChatUser;
