import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { Button, Form } from "reactstrap";
import { compose } from "redux";
import ErrorMsg from "../../../components/ErrorMsg";
import formikEnhancer from "./Enhancers/ChangePasswordEnhancer";
import NavigationActions from "../../../redux/navigation/actions";
import { updateChangePassword } from "../../../api/settings";
import FormInput from "../../../components/FormInput";

const { fetching, success, error } = NavigationActions;
const ChangePassword = (props) => {
  const {
    errors,
    touched,
    submitCount,
    handleChange,
    handleBlur,
    token,
    fetching,
    success,
    error,
    values,
    setValues,
  } = props;

  const [flag, setFlag] = useState(true);

  const Error = (props) => {
    const field = props.field;
    if ((errors[field] && touched[field]) || submitCount > 0) {
      return <ErrorMsg message={errors[field]} />;
    } else {
      return <span />;
    }
  };

  const handleChangePassword = (e) => {
    e.preventDefault();
    let { isValid } = props;
    if (isValid) {
      fetching(true);
      updateChangePassword(token, values)
        .then((res) => {
          if (res.success) {
            setFlag(false);
            fetching(false);
            success(res.message);
            setValues({
              ...values,
              old_password: "",
              new_password: "",
              confirm_password: "",
            });
          } else {
            setFlag(false);
            fetching(false);
            error(res.message);
          }
        })
        .catch((err) => {
          console.log(err);
          fetching(false);
        });
    }
  };

  useEffect(() => {
    if (flag === false) {
      setValues({
        ...values,
        old_password: "",
        new_password: "",
        confirm_password: "",
      });
    }
    // eslint-disable-next-line
  }, [flag]);

  return (
    <div className="accordion-body">
      <Form onSubmit={handleChangePassword} className="position-relative">
        <div>
          <p className="text-muted mb-1">Old Password</p>
          <FormInput
            type="password"
            name="old_password"
            placeholder="Current Password"
            className="form-control"
            onChange={handleChange}
            onBlur={handleBlur}
            values={values.old_password}
          />
          {flag && <Error field="old_password" />}
        </div>

        <div className="mt-4">
          <p className="text-muted mb-1">New Password</p>
          <FormInput
            type="password"
            className="form-control"
            name="new_password"
            placeholder="New Password"
            onChange={handleChange}
            onBlur={handleBlur}
            values={values.new_password}
          />
          {flag && <Error field="new_password" />}
        </div>

        <div className="mt-4">
          <p className="text-muted mb-1">Confirm Password</p>
          <FormInput
            type="password"
            className="form-control"
            name="confirm_password"
            placeholder="Confirm Password"
            onChange={handleChange}
            onBlur={handleBlur}
            values={values.confirm_password}
          />
          {flag && <Error field="confirm_password" />}
        </div>
        <div className="text-center mt-4">
          <Button className="w-100 auth-bg-btn" type="submit">
            Submit
          </Button>
        </div>
      </Form>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
  };
};

export default compose(
  withRouter,
  formikEnhancer,
  connect(mapStateToProps, { fetching, success, error })
)(ChangePassword);
