const { selectedChatData } = ChatAction;

const App = (props) => {
  const { user, selectedChatData } = props;
  const dispatch = useDispatch();

  const { layoutMode } = useSelector((state) => ({
    layoutMode: state.Layout.layoutMode,
  }));

  const colorChange = (color) => {
    const element = document.getElementById(`color-${color}`);
    if (element) {
      const color = window
        .getComputedStyle(element, null)
        .getPropertyValue("background-color");
      const userChatOverlay = document.getElementById("user-chat-overlay");

      if (userChatOverlay) {
        userChatOverlay.style.background = color;
        const rgbColor = color.substring(
          color.indexOf("(") + 1,
          color.indexOf(")")
        );
        document.documentElement.style.setProperty(
          "--bs-primary-rgb",
          rgbColor
        );
        document.documentElement.style.setProperty(
          "--bs-secondary-rgb",
          rgbColor
        );
      }
    }
  };

  // Dark/Light Mode
  useEffect(() => {
    var getLayoutMode = localStorage.getItem("layoutMode");
    if (getLayoutMode) {
      dispatch(changelayoutMode(getLayoutMode));
    } else {
      dispatch(changelayoutMode(layoutMode));
    }
  }, [layoutMode, dispatch]);

  useEffect(() => {
    if (user?.themeColor) {
      colorChange(user?.themeColor);
      selectedChatData(user?.lastSelectedChat);
    }
    // eslint-disable-next-line
  }, [user?.themeColor]);

  return <Routes />;
};

const mapStateToProps = (state) => {
  return {
    user: state.Navigation.user,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, { selectedChatData })
)(App);
