import React, { useEffect, useState } from "react";
import {
  Form,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Label,
  // Input,
} from "reactstrap";
import { searchUser } from "../api/user";
import { Dropdown } from "semantic-ui-react";
import { inviteUser } from "../api/contacts_new";

const InviteContactModal = ({ isOpen, onClose, token, success, fetching }) => {
  /*
  data input handeling
  */
  const [data, setData] = useState({
    friend_id: null,
    message: "",
  });
  const [valid, setValid] = useState(false);

  // eslint-disable-next-line
  const [options, setOptions] = useState([]);

  const onSearchUser = (e) => {
    const { name, value } = e.target;

    if (!name) {
      setData({
        ...data,
        friend_id: value,
      });
    } else {
      setData({
        ...data,
        [name]: value,
      });
    }

    if (!name) {
      fetching(true);
      searchUser(token, value)
        .then((res) => {
          if (res.success) {
            fetching(false);
            setOptions(res.data);
            success();
          }
        })
        .catch((err) => {
          console.log(err);
          fetching(false);
        });
    }
  };

  const onSelectedUser = (e, f) => {
    setData({
      ...data,
      friend_id: f.value,
    });
  };

  const onInvite = () => {
    fetching(true);
    inviteUser(token, data).then((res) => {
      if (res.success) {
        fetching(false);
        onClose();
      }
    });
  };

  /* validation */
  useEffect(() => {
    if (
      !data.message !== null &&
      data.message !== "" &&
      !data.friend_id !== null &&
      data.friend_id !== ""
    ) {
      setValid(true);
    } else {
      setValid(false);
    }
  }, [data]);

  return (
    <Modal isOpen={isOpen} toggle={onClose} tabIndex={-1} centered scrollable>
      <ModalHeader className="modal-title-custom" toggle={onClose}>
        Search New User
      </ModalHeader>
      <ModalBody className="p-4">
        <Form>
          {/* <div className="mb-3">
            <Label htmlFor="AddContactModalemail-input" className="form-label">
              Email
            </Label>
            <Input
              type="email"
              className="form-control"
              id="AddContactModalemail-input"
              placeholder="Enter Email"
              value={data["email"] || ""}
              onChange={(e) => {
                onChangeData("email", e.target.value);
              }}
            />
          </div> */}
          <div className="mb-3">
            <Label htmlFor="AddContactModalname-input" className="form-label">
              Search User
            </Label>

            <Dropdown
              placeholder="Search User"
              name="friend_id"
              fluid
              search
              selection
              options={options}
              onSearchChange={(e) => onSearchUser(e)}
              onChange={(e, f) => onSelectedUser(e, f)}
            />
          </div>
          {/* <div className="mb-3">
            <Input
              type="text"
              className="form-control"
              id="AddContactModalname-input"
              placeholder="Enter Name"
              value={data["name"] || ""}
              onChange={(e) => {
                onChangeData("name", e.target.value);
              }}
            />
          </div> */}
          <div className="mb-3">
            <Label
              htmlFor="AddContactModal-invitemessage-input"
              className="form-label"
            >
              Message
            </Label>
            <textarea
              value={data.message}
              onChange={(e) => onSearchUser(e)}
              className="form-control"
              name="message"
              rows={3}
              placeholder="Enter Message"
            ></textarea>
          </div>
        </Form>
      </ModalBody>
      <ModalFooter>
        <Button type="button" color="link" className="btn" onClick={onClose}>
          Close
        </Button>
        {/* <Button
          type="button"
          color="primary"
          disabled={!valid}
          onClick={() => onInvite(data)}
        >
          Invite
        </Button> */}

        <Button
          type="button"
          className="btn btn-primary w-lg"
          disabled={!valid}
          onClick={() => onInvite(data)}
        >
          Invite
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default InviteContactModal;
