const authActions = {
  LOGIN: "LOGIN",
  LOGOUT: "LOGOUT",

  loginData: (data) => {
    return {
      type: authActions.LOGIN,
      isUserLogin: data.isUserLogin,
      user_token: data.token,
    };
  },

  logoutUser: () => {
    return {
      type: authActions.LOGOUT,
      isUserLogin: false,
      user_token: null,
      user: {},
    };
  },
};

export default authActions;
