import settingsAction from "./actions";

const initState = {
  displayprofilePhoto: "",
  displayLastSeen: "",
  displayStatus: "",
  readReceipts: "",
  displayGroups: "",
  ssn: "",
};

export default function rootReducer(state = initState, action) {
  switch (action.type) {
    case settingsAction.SETTINGS:
      return {
        ...state,
        displayprofilePhoto: action.displayprofilePhoto,
        displayLastSeen: action.displayLastSeen,
        displayStatus: action.displayStatus,
        readReceipts: action.readReceipts,
        displayGroups: action.displayGroups,
        ssn: action.ssn,
      };

    default:
      return state;
  }
}
