import React, { useEffect } from "react";

// hooks
import { useRedux } from "../../../hooks/index";

// components
import Loader from "../../../components/Loader";
import AppSimpleBar from "../../../components/AppSimpleBar";
import MyProfile from "./MyProfile";
import UserDescription from "./UserDescription";
import Media from "../../../components/Media";
import AttachedFiles from "../../../components/AttachedFiles";

// actions
import { getProfileDetails } from "../../../redux/actions";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { compose } from "redux";

const Index = (props) => {
  const { user } = props;
  // global store
  const { dispatch, useAppSelector } = useRedux();

  const { profileDetails, getProfileLoading, isProfileFetched } =
    useAppSelector((state) => ({
      profileDetails: state.Profile.profileDetails,
      getProfileLoading: state.Profile.getProfileLoading,
      isProfileFetched: state.Profile.isProfileFetched,
    }));

  // get user profile details
  useEffect(() => {
    dispatch(getProfileDetails());
  }, [dispatch]);

  return (
    <div className="position-relative">
      {getProfileLoading && !isProfileFetched && <Loader />}
      <MyProfile basicDetails={profileDetails.basicDetails} props={props} />

      <AppSimpleBar className="p-4 profile-desc">
        <UserDescription userDetails={user} />
        <hr className="my-4" />

        <Media media={profileDetails.media} limit={2} />

        <hr className="my-4" />

        <AttachedFiles attachedFiles={profileDetails.attachedFiles} />
      </AppSimpleBar>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
    user: state.Navigation.user,
  };
};

export default compose(withRouter, connect(mapStateToProps, null))(Index);
