const chatActions = {
  CONTACTS: "CONTACTS",
  SELECTEDCHAT: "SELECTED_CHAT",
  DIRECTMESSAGES: "DIRECTMESSAGES",

  contactsData: (data) => ({
    type: chatActions.CONTACTS,
    contacts: data,
  }),
  selectedChatData: (data) => ({
    type: chatActions.SELECTEDCHAT,
    selectedChat: data,
  }),
  directMessages: (data) => ({
    type: chatActions.DIRECTMESSAGES,
    directMessages: data,
  }),
};

export default chatActions;
