import React from "react";
import { Link, withRouter } from "react-router-dom";
import { Row, Col } from "reactstrap";

import NonAuthLayoutWrapper from "../../components/NonAutnLayoutWrapper";

const Logout = () => {
  return (
    <NonAuthLayoutWrapper>
      <Row className="justify-content-center my-auto">
        <Col sm={8} lg={6} xl={5} className="col-xxl-4">
          <div className="py-md-5 py-4 text-center">
            <div className="avatar-xl mx-auto">
              <div className="avatar-title h1 rounded-circle">
                <i className="bx bxs-user"></i>
              </div>
            </div>
            <div className="mt-4 pt-2">
              <h5>You are Logged Out</h5>
              <p className="text-muted font-size-15">
                Thank you for using{" "}
                <span className="fw-semibold text-dark">Chat Portal</span>
              </p>
              <div className="mt-4">
                <Link
                  to="/login"
                  className="btn auth-bg-btn w-100 waves-effect waves-light"
                >
                  Sign In
                </Link>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </NonAuthLayoutWrapper>
  );
};

export default withRouter(Logout);
