import React, { useState, useEffect } from "react";
import { Button, Collapse } from "reactstrap";
import classnames from "classnames";

// hooks
import { useRedux } from "../../../hooks/index";

// actions
import { getSettings } from "../../../redux/actions";

// constants
import { SETTINGS_COLLAPSES } from "../../../constants";

// components
import Loader from "../../../components/Loader";
import AppSimpleBar from "../../../components/AppSimpleBar";
import UserCoverImage from "./UserCoverImage";
import UserProfile from "./UserProfile";
import PersonalInfo from "./PersonalInfo";
import ThemeSettings from "./ThemeSettings";
import ChangePassword from "./ChangePassword";
import Privacy from "./Privacy";
import Security from "./Security";
import Help from "./Help";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { compose } from "redux";
import NavigationAction from "../../../redux/navigation/actions";
import SettingsAction from "../../../redux/settings_new/actions";
import formikEnhancer from "./Enhancers/ProfileEnhancer";
import {
  updateThemeSettings,
  updatePrivacySettings,
} from "../../../api/settings";

let { check, fetching, success, error } = NavigationAction;
let { privacy } = SettingsAction;

const AccordianItem = ({ item, selectedMenu, onChange }) => {
  const isOpen = selectedMenu && selectedMenu === item.value ? true : false;
  const toggleCollapse = () => {
    if (isOpen) {
      onChange(null);
    } else {
      onChange(item.value);
    }
  };
  return (
    <div className="accordion-item">
      <div className="accordion-header" id="headerpersonalinfo">
        <Button
          color="none"
          className={classnames(
            "accordion-button",
            "font-size-14",
            "fw-medium",
            { collapsed: !isOpen }
          )}
          onClick={toggleCollapse}
          type="button"
        >
          <i className={classnames("text-muted", "me-3", item.icon)}></i>{" "}
          {item.label}
        </Button>
      </div>
      <Collapse
        isOpen={isOpen}
        id="personalinfo"
        className="accordion-collapse"
        aria-labelledby="headerpersonalinfo"
        data-bs-parent="#settingprofile"
      >
        {item.component}
      </Collapse>
    </div>
  );
};
const Index = (props) => {
  const {
    user,
    token,
    check,
    fetching,
    success,
    error,
    privacy,
    privacySettings,
  } = props;

  // global store
  const { dispatch, useAppSelector } = useRedux();

  const { settingsData, getSettingsLoading } = useAppSelector((state) => ({
    settingsData: state.Settings.settings,
    getSettingsLoading: state.Profile.getSettingsLoading,
    isSettingsFetched: state.Profile.isSettingsFetched,
  }));

  // get user settings
  useEffect(() => {
    dispatch(getSettings());
  }, [dispatch]);

  const [settings, setSettings] = useState(settingsData);
  useEffect(() => {
    setSettings(settingsData);
  }, [settingsData]);

  /*
  api calling
  */
  const onChangeThemeData = (field, value) => {
    fetching(true);
    updateThemeSettings(token, {
      themeColor: value.color,
      themeBackground: value.image,
    })
      .then((res) => {
        if (res.success) {
          check(token);
          fetching(false);
          success();
        } else {
          fetching(false);
          error();
        }
      })
      .catch((err) => {
        console.log(err);
        fetching(false);
      });
  };

  const onChangeData = () => {};

  const onChangePrivacySettings = (field, value) => {
    fetching(true);
    updatePrivacySettings(token, {
      profilePhotoSeen: value.displayprofilePhoto,
      lastSeen: value.displayLastSeen,
      statusSeen: value.displayStatus,
      readReceipts: value.readReceipts,
      groupsSeen: value.displayGroups,
    })
      .then((res) => {
        // console.log(res);
        if (res.success) {
          privacy(res.data);
          check(token);
          fetching(false);
          success();
        } else {
          fetching(false);
          error();
        }
      })
      .catch((err) => {
        console.log(err);
        fetching(false);
      });
  };

  /*
  collapse handeling
  */
  const [selectedMenu, setSelectedMenu] = useState(null);

  const collapseItems = [
    {
      value: SETTINGS_COLLAPSES.PROFILE,
      label: "Personal Info",
      icon: "bx bxs-user",
      component: (
        <PersonalInfo user={user} basicDetails={settings.basicDetails} />
      ),
    },
    {
      value: SETTINGS_COLLAPSES.THEME,
      label: "Themes",
      icon: "bx bxs-adjust-alt",
      component: (
        <ThemeSettings
          user={user}
          theme={settings.theme}
          onChangeData={onChangeThemeData}
        />
      ),
    },

    {
      value: SETTINGS_COLLAPSES.PRIVACY,
      label: "Privacy",
      icon: "bx bxs-lock",
      component: (
        <Privacy
          user={user}
          privacy={privacySettings}
          onChangeSettings={onChangePrivacySettings}
        />
      ),
    },
    {
      value: SETTINGS_COLLAPSES.CHANGE_PASSWORD,
      label: "Change Password",
      icon: "bx bx-lock-open",
      component: <ChangePassword />,
    },
    {
      value: SETTINGS_COLLAPSES.SECURITY,
      label: "Security",
      icon: "bx bxs-check-shield",
      component: (
        <Security
          user={user}
          security={settings.security}
          onChangeSettings={onChangeData}
        />
      ),
    },
    {
      value: SETTINGS_COLLAPSES.HELP,
      label: "Help",
      icon: "bx bxs-help-circle",
      component: <Help />,
    },
  ];

  const onChangeCollapse = (id) => {
    setSelectedMenu(id);
  };

  return (
    <div className="position-relative">
      {getSettingsLoading && <Loader />}
      <UserCoverImage basicDetails={settings.basicDetails} props={props} />

      <UserProfile
        basicDetails={settings.basicDetails}
        status={settings.status}
        props={props}
      />
      {/* Start User profile description */}
      <AppSimpleBar className="user-setting">
        <div id="settingprofile" className="accordion accordion-flush">
          {(collapseItems || []).map((item, key) => (
            <AccordianItem
              item={item}
              key={key}
              selectedMenu={selectedMenu}
              onChange={onChangeCollapse}
            />
          ))}
        </div>
        {/* end profile-setting-accordion */}
      </AppSimpleBar>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
    user: state.Navigation.user,
    privacySettings: state.SettingsNew,
  };
};

export default compose(
  withRouter,
  formikEnhancer,
  connect(mapStateToProps, { check, fetching, success, error, privacy })
)(Index);
