import React, { useEffect, useState } from "react";
import { Label, UncontrolledTooltip } from "reactstrap";
import { uploadCoverImage } from "../../../api/user";
import config from "../../../config";

const UserCoverImage = ({ props }) => {
  const { token, check, success, error, fetching, user } = props;
  const [image, setImage] = useState("");

  const onChangeCoverImage = () => {
    if (image.name) {
      let data = new FormData();
      data.append("coverImage", image);
      uploadCoverImage(token, data)
        .then((res) => {
          if (res.success) {
            check(token);
            fetching(false);
            success(res.message);
            setImage("");
          } else {
            error(res.message);
            fetching(false);
            setImage("");
          }
        })
        .catch((err) => {
          console.log(err);
          fetching(false);
          setImage("");
        });
    }
  };

  useEffect(() => {
    if (image.name) {
      onChangeCoverImage();
    }
    // eslint-disable-next-line
  }, [image.name]);
  return (
    <div className="user-profile-img">
      {user?.coverImage && (
        <img
          src={`${config.BACKENDURL}/coverImages/${user?.coverImage}`}
          className="profile-img profile-foreground-img"
          style={{ height: "160px" }}
          alt=""
        />
      )}

      <div className="overlay-content">
        <div>
          <div className="user-chat-nav p-3">
            <div className="d-flex w-100 align-items-center">
              <div className="flex-grow-1">
                <h5 className="text-white mb-0">Settings</h5>
              </div>
              <div className="flex-shrink-0">
                <div
                  className="avatar-xs p-0 rounded-circle profile-photo-edit"
                  id="change-cover"
                >
                  <input
                    id="profile-foreground-img-file-input"
                    type="file"
                    accept="image/png, image/jpeg"
                    className="profile-foreground-img-file-input"
                    onChange={(e) => {
                      if (e.target.files.length > 0) {
                        var fileArr = Array.from(e.target.files);
                        if (fileArr?.length > 0) {
                          setImage(fileArr[0]);
                        } else {
                          setImage("");
                        }
                      }
                    }}
                  />
                  <Label
                    htmlFor="profile-foreground-img-file-input"
                    className="profile-photo-edit avatar-xs"
                  >
                    <span className="avatar-title rounded-circle bg-light text-body">
                      <i className="bx bxs-pencil"></i>
                    </span>
                  </Label>
                </div>
                <UncontrolledTooltip target="change-cover" placement="bottom">
                  Change Background
                </UncontrolledTooltip>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default UserCoverImage;
